@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap);
*,
::before,
::after {
  box-sizing: border-box;
}

p,
ul {
  margin: 0;
  padding: 0;
}
li {
  list-style: none;
}

body {
  margin: 0;
  font-family: "Open Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.profile__container {
  margin: 20px 0 0 20px;
  padding-top: 30px;
  width: 250px;
  outline: 1px solid gray;
  box-shadow: 10px 10px 8px 2px rgba(0, 0, 0, 0.3);
}

.profile__avatar {
  display: block;
  margin: 0 auto;
  margin-bottom: 30px;
  border-radius: 50%;
  border: 2px solid firebrick;
}

.profile__text {
  text-align: center;
  margin-bottom: 15px;
}

.profile__text__location {
  margin-bottom: 30px;
}

.profile__title {
  color: #000;
  font-weight: bold;
}

.profile__stats {
  display: flex;
}

.profile__list__item {
  flex-basis: 100px;
  padding: 20px 0 25px 0;
  outline: 1px solid gray;
  background-color: #f4f4f4;
}

.profile__label {
  display: block;
  margin: 0 auto;
  text-align: center;
}

.profile__quantity {
  display: block;
  font-weight: bold;
  text-align: center;
}

.stats__container {
  margin: 20px 0 20px 20px;
  display: inline-block;
  outline: 1px solid gray;
  box-shadow: 10px 10px 8px 2px rgba(0, 0, 0, 0.3);
}

.stats__label {
  display: block;
  text-align: center;
}

.stats__percentage {
  display: block;
  text-align: center;
}

.stats__item {
  outline: 1px solid salmon;
  padding: 15px 0 15px 0;
  width: 80px;
  height: 80px;
}

.stats__list {
  display: flex;
}

.stats__title {
  text-align: center;
  font-weight: bold;
  margin: 0;
  padding: 30px 0 30px 0;
  text-transform: uppercase;
}

.friendList__container {
  margin: 20px 0 20px 20px;
}

.friendList__item {
  border: 1px solid gray;
  width: 280px;
  padding: 8px 0 8px 15px;
  margin-bottom: 10px;
  box-shadow: 5px 5px 4px 1px rgba(0, 0, 0, 0.3);
}

.statusOnline::before {
  margin-right: 15px;
  width: 25px;
  height: 25px;
  display: inline-block;
  background-color: green;
  border-radius: 50%;
  content: "";
}

.statusOffline::before {
  margin-right: 15px;
  width: 25px;
  height: 25px;
  display: inline-block;
  background-color: red;
  border-radius: 50%;
  content: "";
}

.avatar {
  margin-right: 15px;
}

.friendListItem__name {
  display: inline-block;
}

.tr__container {
  outline: 1px solid red;
  margin-left: auto;
  margin-right: auto;
  width: 750px;
  outline: 1px solid gray;
  box-shadow: 10px 10px 8px 2px rgba(0, 0, 0, 0.3);
}

.tr__item {
  padding: 10px 0 10px 0;
  text-align: center;
  outline: 1px solid gray;
}

.tr__head {
  background-color: aquamarine;
  padding: 10px 0 10px 0;
}

.tr__active {
  background-color: #f4f488;
}

